/* eslint-disable react/no-unescaped-entities */
/* eslint-disable @typescript-eslint/no-unused-vars */
"use client"

import { useState, useEffect } from "react"
import { Loader2, X, Check } from "lucide-react"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { Checkbox } from "@/components/ui/checkbox"
import { Label } from "@/components/ui/label"
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group"

// Interfaces for data
interface Group {
  idGroupe: string
  nomGroupe: string
}

interface Jury {
  idJury: number
  nom: string
}

interface FormData {
  jury: string[]
  groups: string[]
}

// Initial form state
const INITIAL_FORM_STATE: FormData = {
  jury: [],
  groups: [],
}

// Sample data (replace with API data)
const groupsData: Group[] = [
  { idGroupe: "1", nomGroupe: "Group A" },
  { idGroupe: "2", nomGroupe: "Group B" },
  { idGroupe: "3", nomGroupe: "Group C" },
]

const jurysData: Jury[] = [
  { idJury: 1, nom: "Jury 1" },
  { idJury: 2, nom: "Jury 2" },
  { idJury: 3, nom: "Jury 3" },
]

export default function PlanifierSoutenance() {
  const [formData, setFormData] = useState<FormData>(INITIAL_FORM_STATE)
  const [groups, setGroups] = useState<Group[]>([])
  const [jurys, setJurys] = useState<Jury[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [toast, setToast] = useState<{ message: string; type: "success" | "error" } | null>(null)
  const [mode, setMode] = useState<"manual" | "aleatoire" | "semi-aleatoire">("manual")

  // Fetch data (simulated here, replace with real API calls)
  useEffect(() => {
    const fetchData = async () => {
      try {
        setLoading(true)
        // Simulate API fetch
        setGroups(groupsData)
        setJurys(jurysData)
      } catch (error) {
        setError("Error loading data.")
      } finally {
        setLoading(false)
      }
    }
    fetchData()
  }, [])

  // Handle jury selection based on mode
  const handleJuryChange = async (juryId: string, checked: boolean) => {
    let newJury = [...formData.jury]

    if (mode === "aleatoire") {
      // Aleatoire: Only one jury allowed
      if (checked && newJury.length === 0) {
        newJury = [juryId]
        try {
          const response = await fetch(`http://localhost:5000/api/soutenance/jury-groups/${juryId}`)
          const associatedGroups = await response.json()
          if (associatedGroups.length > 0) {
            setFormData({ jury: newJury, groups: [associatedGroups[0].idGroupe] })
            setToast({ type: "success", message: "Group auto-selected!" })
          }
        } catch (error) {
          setToast({ type: "error", message: "Error fetching group." })
        }
      } else if (!checked) {
        newJury = []
        setFormData({ jury: [], groups: [] })
      }
    } else if (mode === "semi-aleatoire") {
      // Semi-Aleatoire: 2 or 3 jurys
      if (checked && newJury.length < 3) {
        newJury.push(juryId)
      } else if (!checked) {
        newJury = newJury.filter((id) => id !== juryId)
      }

      if (newJury.length >= 2) {
        try {
          const groupPromises = newJury.map((id) =>
            fetch(`http://localhost:5000/api/soutenance/jury-groups/${id}`).then((res) => res.json())
          )
          const groupsArray = await Promise.all(groupPromises)
          const allGroups = groupsArray.flat().map((g) => g.idGroupe)
          const uniqueGroups = [...new Set(allGroups)]
          setFormData({ jury: newJury, groups: uniqueGroups })
          setToast({ type: "success", message: "Groups auto-selected!" })
        } catch (error) {
          setToast({ type: "error", message: "Error fetching groups." })
        }
      } else {
        setFormData({ jury: newJury, groups: [] })
      }
    } else {
      // Manual mode
      if (checked) newJury.push(juryId)
      else newJury = newJury.filter((id) => id !== juryId)
      setFormData({ ...formData, jury: newJury })
    }
  }

  // Handle group selection (manual mode only)
  const handleGroupChange = (groupId: string, checked: boolean) => {
    if (mode === "manual") {
      setFormData((prev) => ({
        ...prev,
        groups: checked
          ? [...prev.groups, groupId]
          : prev.groups.filter((id) => id !== groupId),
      }))
    }
  }

  // Submit form (simplified for demo)
  const onAddSoutenance = () => {
    if (formData.jury.length === 0 || formData.groups.length === 0) {
      setToast({ type: "error", message: "Pick jurys and groups!" })
      return
    }
    console.log("Soutenance planned:", formData)
    setToast({ type: "success", message: "Soutenance planned!" })
    setFormData(INITIAL_FORM_STATE)
  }

  if (loading) return <Loader2 className="h-12 w-12 animate-spin" />
  if (error) return <Alert variant="destructive"><AlertDescription>{error}</AlertDescription></Alert>

  return (
    <div className="p-4">
      {toast && (
        <Alert className="mb-4">
          <AlertDescription>{toast.message}</AlertDescription>
          <Button variant="ghost" onClick={() => setToast(null)}>
            <X className="h-5 w-5" />
          </Button>
        </Alert>
      )}

      <Card>
        <CardHeader>
          <CardTitle>Plan Soutenance</CardTitle>
        </CardHeader>
        <CardContent className="grid gap-6">
          {/* Mode Selector */}
          <div>
            <Label>Mode</Label>
            <RadioGroup
              value={mode}
              onValueChange={(value) => {
                setMode(value as "manual" | "aleatoire" | "semi-aleatoire")
                setFormData(INITIAL_FORM_STATE) // Reset form when mode changes
              }}
              className="flex gap-4"
            >
              <div className="flex items-center space-x-2">
                <RadioGroupItem value="manual" id="manual" />
                <Label htmlFor="manual">Manual</Label>
              </div>
              <div className="flex items-center space-x-2">
                <RadioGroupItem value="aleatoire" id="aleatoire" />
                <Label htmlFor="aleatoire">Aleatoire (1 jury, auto group)</Label>
              </div>
              <div className="flex items-center space-x-2">
                <RadioGroupItem value="semi-aleatoire" id="semi-aleatoire" />
                <Label htmlFor="semi-aleatoire">Semi-Aleatoire (2-3 jurys, auto groups)</Label>
              </div>
            </RadioGroup>
          </div>

          {/* Jury Selection */}
          <div>
            <Label>Jury</Label>
            <div className="space-y-2 max-h-60 overflow-auto border p-2 rounded-md">
              {jurys.map((jury) => (
                <div key={jury.idJury} className="flex items-center space-x-2">
                  <Checkbox
                    id={`jury-${jury.idJury}`}
                    checked={formData.jury.includes(jury.idJury.toString())}
                    onCheckedChange={(checked) => handleJuryChange(jury.idJury.toString(), checked === true)}
                    disabled={mode === "aleatoire" && formData.jury.length === 1 && !formData.jury.includes(jury.idJury.toString())}
                  />
                  <Label htmlFor={`jury-${jury.idJury}`}>{jury.nom}</Label>
                </div>
              ))}
            </div>
            {mode === "semi-aleatoire" && formData.jury.length > 3 && (
              <Alert variant="destructive">
                <AlertDescription>Max 3 jurys in Semi-Aleatoire mode!</AlertDescription>
              </Alert>
            )}
          </div>

          {/* Group Selection */}
          <div>
            <Label>Groups</Label>
            <div className="space-y-2 max-h-60 overflow-auto border p-2 rounded-md">
              {groups.map((group) => (
                <div key={group.idGroupe} className="flex items-center space-x-2">
                  <Checkbox
                    id={`group-${group.idGroupe}`}
                    checked={formData.groups.includes(group.idGroupe)}
                    onCheckedChange={(checked) => handleGroupChange(group.idGroupe, checked === true)}
                    disabled={mode !== "manual"}
                  />
                  <Label htmlFor={`group-${group.idGroupe}`}>{group.nomGroupe}</Label>
                </div>
              ))}
            </div>
          </div>

          {/* Submit */}
          <Button onClick={onAddSoutenance}>Plan It!</Button>
        </CardContent>
      </Card>
    </div>
  )
}